@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: 'Roboto',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%); */
  background-color: #000a12;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cardDetailOuter{
  color: white;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 20px;
}

.cardDetailOuter > h3{
  width: 80%;
  text-align: left;
}

.cardInnerLeft{
  /* border-radius: 5px; */
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: #263238;
  color: white;
  align-items: center;
  width: 350px;
  height: 600px;
  cursor: default;
}
.cardInnerLeft > div > h2 {
  color: lightcoral;
}

.cardDetail{
  /* border-radius: 5px; */
  margin: 20px 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: #263238;
  color: white;
  align-items: center;
  width: 350px;
  height: 600px;
  cursor: default;
}


.cardDetail_heading{
  height: 10%;
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 0.5rem;
  color: lightcoral;
}
.cardDetail_mid{
  height: 40%;
  display: flex;
  cursor: default;
  justify-content: center;
  width: 85%;
}
.cardDetail_mid > img {
  width: 100%;
  cursor: default;
}

.cardDetail_foot{
  height: 40%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  overflow: hidden;
  width: 85%;
  text-align: left;
}

.cardDetail_foot > h4{
  color: lightcoral;
  margin: 10px 0 5px 0;
}

h4 > span {
  color: white;
  font-size: 1rem;
  margin-left: 5px;
}

.cardDetail_foot > p {
  font-size: 0.8rem;
  margin: 1.5px;
}

span{
  font-weight: 600;
  color: lightcoral;
}

.cardDetailFooter1 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 10%;
  text-align: right;
  width: 85%;
}
.seemore{
  color: lightcoral;
  cursor: pointer;
  padding: 5px;
}
.bookmark{
  cursor: pointer;
}

.ingCardTop{
  height: 10%;
  width: 85%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 0.5rem;
  color: lightcoral;
}

.ingCardBot {
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  overflow: hidden;
  width: 85%;
  text-align: left;
}

.ings {
  margin: 0 0 10px 0;
  font-size: 0.9rem;
}

@media only screen 
  and (min-device-width: 414px) 
  and (max-device-width: 736px) 
  and (-webkit-min-device-pixel-ratio: 3) { 
  .orderTop{order: 3}
  .orderMid{order: 1}
  .orderBot{order:2}
}
.cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 10px;
}

.card {
  border-radius: 5px;
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: #263238;
  color: white;
  align-items: center;
  width: 350px;
  height: 600px;
  cursor: default;
}


.card_heading{
  height: 10%;
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 0.5rem;
  color: lightcoral;
}
.card_mid{
  height: 40%;
  display: flex;
  cursor: pointer;
}

.card_foot{
  height: 40%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  overflow: hidden;
  width: 85%;
  text-align: left;
}

.card_foot > h4{
  margin: 10px 0 5px 0;
  color: lightcoral;
}

.card_foot > p {
  font-size: 0.8rem;
  margin: 1.5px;
}

span{
  font-weight: 600;
  color: lightcoral;
}

.footer1 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 10%;
  text-align: right;
  width: 85%;
}
.seemore{
  color: lightcoral;
  cursor: pointer;
  padding: 5px;
}
.bookmark{
  cursor: pointer;
  justify-self: flex-start;
}
.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 80%;
  margin: auto;
}

header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

header > h1 {
  color: coral;
  cursor: default; 
}
a{
  text-decoration: none;
  border: 1px solid lightcoral ;
  color: coral;
  padding: 5px 10px;
  border-radius: 3px;
  font-size: 1.1rem;
  cursor: pointer;
}
.bookmark{
  cursor: pointer;
}
.bookmark:hover{
  fill:#f08080;
  color: #f08080;
}
form{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 10px;
}

input{
  width: 80%;
  height: 30px;
  border: none;
  /* background-color: #4f5b62; */
  background-color: #263238;
  color: whitesmoke;
  text-align: center;
  font-size: 1rem;
  outline: none;
}

form > button {
  background-color: lightcoral;
  border: none;
  color: white;
  min-width: 80px;
  width: 10%;
  height: 32px;
  cursor: pointer;
  outline: none;
}

form > button:hover{
  background-color: coral;
}
